import React from 'react';
import './slider.css';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";
import s1 from '../../images/slider1.png';
import s2 from '../../images/slider2.png';
import s3 from '../../images/slider7.png';
import s4 from '../../images/slider4.jpg';
export default function Carousel() {
    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: false,
        centerPadding: 30,
        responsive: [
          {
            breakpoint: 960,
            settings: {
              slidesToShow: 2,
              slidesToScroll: 2,
              infinite: true,
              dots: true,
            }
          },
          {
            breakpoint: 568,
            settings: {
              slidesToShow: 1,
              slidesToScroll: 1,
              infinite: true,
              dots: true,
            }
          }
          ],
          
      }
    return (
        <div className="carousel">
        <Slider {...settings}>
          <div>
            <img src={s1}/>
          </div>
          <div>
          <img src={s2}/>
          </div>
          <div>
          <img src={s3}/>
          </div>
          <div>
          <img src={s4}/>
          </div>  
        </Slider>
      </div>
    )
}
