import React from 'react';
import './recentdeal.css';
import Carousel from '../slider/slider'
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import price from '../../images/price3.png';
import PhoneIphoneIcon from '@material-ui/icons/PhoneIphone';
import CancelIcon from '@material-ui/icons/Cancel';
import PhoneIcon from '@material-ui/icons/Phone';
const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    marginTop:80
  },
  icon: {
    fontSize: 15,
   color:'#0099ff'
  },
  call:{
    fontSize: 15,
    color:'#0099ff',
  }
}));
export default function Recentdeal() {
  const classes = useStyles();
    return (
    <Grid item xs={12} sm={12} md={4} className="right">

      <div className="recent" id="scr">
      <img src={price}/>
       <h2 className="rctag">Recent Deals</h2>
      </div>

      <Carousel/>

      <Grid container className="visits">
      <Grid item xs={5}>
      <div className="visit">
      <p>Total Visits</p>
      <h2>256</h2>
      </div>
      </Grid>

      <Grid item xs={7}>
      <div className="visit visit2">
     <p>Last Visit</p>
     <h2>30 Days Back</h2>
      </div>
      </Grid>

      <Grid item xs={12}>
      <div className="phone">
      <div className="picon">
      <PhoneIphoneIcon className="ph"/>
      </div>
      <div className="">
      <p> Register to device</p>
      <p> 900089999</p>
      </div>
      <div className="unlink">
      <CancelIcon className={classes.icon}/> <span>Unlink Device</span>
      </div>
      </div>
      </Grid>
      </Grid>

      <div className="registerHeading">
      <h3>Are you a part of</h3>
      <h2>OPENWIFI - OFFICE</h2>
      <p>Yes, I am from</p>
      </div>
    
      <Grid container className="register">
      <Grid item xs={6}>
      <div className="slct">
       <select>
        <option>Select group</option>
        <option>option1</option>
        <option>option2</option>
        <option>option3</option>
      </select>
      
      </div>
      </Grid>
      <Grid item xs={6}>
      <div className="reg">
      <button>Register</button>
      </div>
      </Grid> 
      </Grid>
      <div className="footer">
    <PhoneIcon className={classes.call}/> <span style={{color:'grey'}}>Support - Contact admin Krishna @8999977998</span>
      </div>
       

     
      </Grid>
    )
}
